import React, { createContext, useState, useEffect } from "react";

const PoolContext = createContext();

export { PoolContext };

const PoolContextProvider = (props) => {
    const [polution, setPolution] = useState(0.00)
    const [prevPolution, setPrevPolution] = useState(0.00)
    const [qualite, setQualite] = useState(1.00)
    const [prevQualite, setPrevQualite] = useState(1)
    const [lightButton, setLightButton] = useState(false)
    const [chloreButton, setChloreButton] = useState(false)
    const [filtrationButton, setFiltrationButton] = useState(false)
    const [intrusion, setIntrusion] = useState(false)

    const getRandomArbitrary = (min, max) => {
        return Math.random() * (max - min) + min;
    }
    
    useEffect(() => {
        const interval = setInterval(() => {
            chloreButton === false && setPolution(polution => {
                let calcul = polution + getRandomArbitrary(0,0.1)
                if(calcul > 1) calcul = 1
                setPrevPolution(prevPolution => {
                    return polution
                })
                if(polution >= 0 && calcul <= 1){
                    return calcul
                }else{
                    return polution
                }
            })

            filtrationButton === false && setQualite(qualite => {
                let calcul = qualite - getRandomArbitrary(0,0.1)
                if(calcul < 0) calcul = 0
                setPrevQualite(prevQualite => {
                    return qualite
                })
                if(calcul >= 0 && qualite <= 1){
                    return calcul
                }else{
                    return qualite
                }
            })

            //Intrusion 

            const intrusionRandom = parseInt(getRandomArbitrary(0, 100))
            if(intrusionRandom <= 2 && lightButton === false) hasIntrusion()


            }, 2000);
            return () => clearInterval(interval);
    }, [chloreButton, filtrationButton, lightButton])

    const addChlore = () => {
        setPrevPolution(0);
        setPolution(0)
    }

    const filtration = () => {
        setPrevQualite(1);
        setQualite(1)
    }

    const onClickLight = () => {
        setLightButton(!lightButton)
        if(intrusion) setIntrusion(false)
    }

    const onClickChlore = () => {
        !chloreButton && addChlore();
        setChloreButton(!chloreButton)
    }

    const onClickFiltration = () => {
        !filtrationButton && filtration();
        setFiltrationButton(!filtrationButton)
    }

    const hasIntrusion = () => {
        if(lightButton === false) setIntrusion(true)
    }

  return (
    <PoolContext.Provider 
    value={{ 
        polution,
        setPolution,
        prevPolution,
        setPrevPolution,
        qualite,
        setQualite,
        prevQualite,
        setPrevQualite,
        lightButton,
        onClickLight,
        chloreButton,
        onClickChlore,
        filtrationButton,
        onClickFiltration,
        intrusion }}>
      {props.children}
    </PoolContext.Provider>
  );
};

export default PoolContextProvider;