import React, {useState, useEffect, useContext} from 'react'
import CountUp from 'react-countup';

import { PoolContext } from "../../context/PoolContext";

import './pool.scss'

export default function Pool() {

    const [classPolutionPool, setClassPolutionPool] = useState('')
    const [classQualitePool, setClassQualitePool] = useState('')
    const [warningFiltration, setWarningFiltration] = useState(false)
    const [warningChlore, setWarningChlore] = useState(false)

    const { polution,
        prevPolution,
        qualite,
        prevQualite,
        lightButton,
        onClickLight,
        chloreButton,
        onClickChlore,
        filtrationButton,
        onClickFiltration,
        intrusion
    } = useContext(PoolContext);

    useEffect(() => {
            if(polution >= 0 && polution < 0.25) setClassPolutionPool('')
            if(polution >= 0.25 && polution < 0.5) setClassPolutionPool('little-dirty')
            if(polution >= 0.5 && polution < 0.75) setClassPolutionPool('semi-dirty')
            if(polution >= 0.75 && polution < 1) setClassPolutionPool('almost-dirty')
            if(polution >= 1){
                setClassPolutionPool('dirty')
                setWarningChlore(true)
            }else{
                setWarningChlore(false)
            }
    }, [polution])

    useEffect(() => {
            if(qualite >= 0 && qualite < 0.25) setClassQualitePool('trouble')
            if(qualite >= 0.25 && qualite < 0.5) setClassQualitePool('almost-trouble')
            if(qualite >= 0.5 && qualite < 0.75) setClassQualitePool('semi-trouble')
            if(qualite >= 0.75 && qualite < 1) setClassQualitePool('little-trouble')
            if(qualite >= 1) setClassQualitePool('')

            if(qualite === 0){
                setWarningFiltration(true)
            }else{
                setWarningFiltration(false)
            }
    }, [qualite])
    
    return (
        <div className="pool-container d-flex">

            <div className={`pool container-fluid ${classPolutionPool} ${classQualitePool} ${intrusion && 'intrusion'}`}>
                <div className="swim-pool-container">
                    <div className="swim-pool-container-left">
                        <div className={`light ${!lightButton && 'off'}`}></div>
                        <div className={`light ${!lightButton && 'off'}`}></div>
                    </div>
                    <div className="swim-pool-container-right">
                        <div className={`light ${!lightButton && 'off'}`}></div>
                        <div className={`light ${!lightButton && 'off'}`}></div>
                    </div>

                    <div className="stairs">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                </div>
                <div className="buttons">
                    <div className={`button ${intrusion && 'warning'} ${lightButton && 'active'}`} onClick={onClickLight}>Lumière</div>
                    <div className={`button ${warningChlore && 'warning'} ${chloreButton && 'active'}`} onClick={onClickChlore}>Chlore</div>
                    <div className={`button ${warningFiltration && 'warning'} ${filtrationButton && 'active'}`} onClick={onClickFiltration}>Pompe de filtration</div>
                </div>
            </div>

            <div className="indicators">
                <CountUp
                    start={parseInt((prevPolution) * 100)}
                    end={parseInt((polution) * 100)}
                    duration={1.9}
                    separator=" "
                    suffix=" %"
                    >
                    {({ countUpRef, start }) => (
                        <div className="polution">
                            <span className="percentage" ref={countUpRef} />
                            <span className="title-indicator">Polution de l'eau</span>
                        </div>
                    )}
                </CountUp>
                <CountUp
                    start={parseInt((prevQualite) * 100)}
                    end={parseInt((qualite) * 100)}
                    duration={1.9}
                    separator=" "
                    suffix=" %"
                    >
                    {({ countUpRef, start }) => (
                        <div className="quality">
                            <span className="percentage" ref={countUpRef} />
                            <span className="title-indicator">Qualité de l'eau</span>
                        </div>
                    )}
                </CountUp>
                
            </div>
        </div>
    )
}
